@import 'variables';
@font-face {
	font-family: $font-name1;
	src: url('../fonts/'+$font-name1);
	src: local($font-name1), local($font-name1),
		url('../fonts/'+$font-name1+'.woff2') format('woff2'), 
		url('../fonts/'+$font-name1+'.woff') format('woff'), 
		url('../fonts/'+$font-name1+'.ttf') format('truetype');
}